<template>
  <div class="gallery" v-if="show">
    <div class="background" @click="close"></div>
    <img class="item" :src="images[now].imgSrc">
    <div class="control">
      <div @click="move(-1)">&#60; </div>
      <div @click="move(1)"> &#62;</div>
      <div @click="close">X</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Gallery",
  props: ['images'],
  computed: {
    now () {
      return this.$store.state.now
    },
    show () {
      return this.$store.state.show
    },
  },
  methods: {
    close() {
      this.$store.dispatch('setShow', false)
    },
    move(ind) {
      this.$store.dispatch('setNow', (6 + this.now + ind) % 6)
    }
  }
}
</script>

