<template>
  <div class="map">
    <div id="map"></div>
  </div>
</template>

<script>
export default {
  name: "Map",
  data() {
    return {
      map: null
    }
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.loadMap()
    } else {
      this.loadScript()
    }
  },
  methods: {
    loadScript() {
      const script = document.createElement("script")
      script.src = "//dapi.kakao.com/v2/maps/sdk.js?appkey=31bb23389e678402d21c56e33aa67c6c&autoload=false"
      script.onload = () => window.kakao.maps.load(this.loadMap)
      document.head.appendChild(script)
    },
    loadMap() {
      const container = document.getElementById('map'); //지도를 담을 영역의 DOM 레퍼런스
      const latitude = 35.14674341486056
      const longitude = 126.84066929821394

      const options = { //지도를 생성할 때 필요한 기본 옵션
        center: new kakao.maps.LatLng(latitude, longitude), //지도의 중심좌표.
        level: 3 //지도의 레벨(확대, 축소 정도)
      };
      this.map = new kakao.maps.Map(container, options); //지도 생성 및 객체 리턴
      this.loadMaker(latitude, longitude)
    },
    loadMaker(latitude, longitude) {
      const makerPosition = new window.kakao.maps.LatLng(latitude, longitude)
      const maker = new window.kakao.maps.Marker({
        position: makerPosition
      })

      maker.setMap(this.map)
    }
  }
}
</script>

<style scoped lang="scss">
  .map {
    display: flex;
    align-content: center;
    justify-content: center;
    padding: 0 36px 0 36px;

    #map {
      width: 100%;
      height: 360px;
    }
  }
</style>