import { createStore } from 'vuex'

export default createStore({
  state: {
    show: false,
    now: 0
  },
  getters: {
    show: state => state.show,
    now: state => state.now,
  },
  mutations: {
    show: (state, payload) => state.show = payload,
    now: (state, payload) => state.now = payload,
  },
  actions: {
    setShow: (store, show) => {
      store.commit('show', show)
    },
    setNow: (store, now) => {
      store.commit('now', now)
    }
  },
  modules: {
  }
})
